import * as equal from 'fast-deep-equal';
export const isEqual = (objA, objB) => {
    return equal(objA, objB);
};
export const isArray = (item) => {
    return Array.isArray(item);
};
// https://github.com/reduxjs/redux/blob/master/src/utils/isPlainObject.ts
export const isPlainObject = (item) => {
    if (typeof item !== 'object' || item === null) {
        return false;
    }
    let proto = item;
    while (Object.getPrototypeOf(proto) !== null) {
        proto = Object.getPrototypeOf(proto);
    }
    return Object.getPrototypeOf(item) === proto;
};
export const isNumber = (item) => {
    return typeof item === 'number';
};
export const isString = (item) => {
    return typeof item === 'string';
};
export const isUndefined = (item) => {
    return typeof item === 'undefined';
};
export const isDefined = (item) => {
    return typeof item !== 'undefined';
};
